import React, { Component } from 'react';

class Referente extends Component {
    constructor(props) {
        super(props);
        this.state = {
            contacts: []
        }

    }
    setContacts(array) {
        this.setState({
            contacts: array
        })
    }
    getContacts() {
        return this.state.contacts;
    }
}
export default new Referente();