import React, { Component } from "react";
import ContactDataService from "../services/contact.service";
import { Link } from "react-router-dom";
import { DataGrid, GridToolbar } from '@material-ui/data-grid';

import { styles } from "../css-common"
import { TextField, Button, Grid, ListItem, withStyles } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputBase from '@material-ui/core/InputBase';
import Referente from "./referente";


const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

const BootstrapInput = withStyles((theme) => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 26px 10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        // Use the system font instead of the default Roboto font.
        fontFamily: [
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        '&:focus': {
            borderRadius: 4,
            borderColor: '#80bdff',
            boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
        },
    },
}))(InputBase);

class ContactComponent extends Component {
    constructor(props) {
        super(props);
        this.onChangeSearchValue = this.onChangeSearchValue.bind(this);
        this.retrieveContacts = this.retrieveContacts.bind(this);
        this.refreshList = this.refreshList.bind(this);
        this.setActiveContact = this.setActiveContact.bind(this);
        this.removeAllContacts = this.removeAllContacts.bind(this);
        this.searchHead = this.searchHead.bind(this);
        this.setHead = this.setHead.bind(this);

        this.state = {
            contacts: [],
            currentContact: null,
            currentIndex: -1,
            head: "nome",
            searchValue: ""
        };
    }


    componentDidMount() {
        this.retrieveContacts();
    }

    onChangeSearchValue(e) {
        const searchValue = e.target.value;
        this.setState({
            searchValue
        });
    }


    retrieveContacts() {
        ContactDataService.getAll(this.props.user)
            .then(response => {
                this.setState({
                    contacts: response.data
                });
            })
            .catch(e => {
                console.log(e);
            });
        Referente.setContacts(this.state.contacts);
    }


    refreshList() {
        this.retrieveContacts();
        this.setState({
            currentContact: null,
            currentIndex: -1
        });
    }

    setActiveContact(contact, index) {
        console.log(contact);
        this.setState({
            currentContact: contact,
            currentIndex: index
        });
    }

    removeAllContacts() {
        ContactDataService.deleteAll()
            .then(response => {
                console.log(response.data);
                this.refreshList();
            })
            .catch(e => {
                console.log(e);
            });
    }

    searchHead() {
        ContactDataService.findByHead(this.props.user, this.state.head, this.state.searchValue)
            .then(response => {
                this.setState({
                    contacts: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    setHead = (event) => {
        this.setState({
            head: event.target.value
        });
    }

    render() {
        const { classes } = this.props
        const { searchValue, contacts, currentContact, currentIndex } = this.state;

        return (
            <div className={classes.form}>
                <Grid container>
                    <Grid className={classes.search} item sm={12} xs={12} md={12} xl={12} lg={12}>
                        <TextField
                            variant="outlined"
                            label="Cerca"
                            value={searchValue}
                            onChange={this.onChangeSearchValue}
                        />
                        &emsp;
                        <NativeSelect
                            id="demo-customized-select-native"
                            onChange={(e) => this.setHead(e)}
                            input={<BootstrapInput />}
                        >
                            <option value="nome">Nome</option>
                            <option value="cognome">Cognome</option>
                            <option value="nome_azienda">Ragione sociale</option>
                            <option value="referente">Referente</option>
                            <option value="fornitore_luce">Fonitore luce</option>
                            <option value="fornitore_gas">Fornitore gas</option>

                        </NativeSelect>
                        &emsp;
                        <Button
                            size="small"
                            variant="outlined"
                            className={classes.textField}
                            onClick={this.searchHead}>
                            Cerca
                        </Button>

                    </Grid>
                    <Grid item md={8}>
                        <h2>Contatti</h2>
                        <div style={{ height: 400, width: '100%' }}>
                            <div style={{ display: 'flex', height: '100%' }}>
                                <div style={{ flexGrow: 1 }}>
                                    <DataGrid onRowClick={(row, e) => this.setActiveContact(row.row, row.id)} disableColumnMenu
                                        columns={[{ field: 'id', hide: true },
                                        { field: 'nome', width: 150, headerName: "Nome" },
                                        { field: 'numero', with: 'id', headerName: "Numero" },
                                        { field: 'cognome', width: 150, headerName: "Cognome" },
                                        { field: 'telefono_ufficio', width: 150, hide: true },
                                        { field: 'nome_azienda', width: 250, headerName: "Ragione Sociale" },
                                        { field: 'cellulare', width: 150, headerName: "Cellulare" },
                                        { field: 'telefono_casa', width: 150, hide: true },
                                        { field: 'telefono_secondario', width: 150, hide: true },
                                        { field: 'email_principale', width: 150, headerName: "Email" },
                                        { field: 'codice_fiscale', width: 150, hide: true },
                                        { field: 'compleanno', width: 150, hide: true },
                                        { field: 'orario_modifica', width: 150, hide: true },
                                        { field: 'assegnato_a', width: 150, hide: true },
                                        { field: 'last_modified_by', width: 150, hide: true },
                                        { field: 'note_importazione', width: 150, hide: true },
                                        { field: 'primo_contatto', width: 150, hide: true },
                                        { field: 'numero_documento', width: 150, hide: true },
                                        { field: 'rilasciato_da', width: 150, hide: true },
                                        { field: 'rilasciato_il', width: 150, hide: true },
                                        { field: 'nato_a', width: 150, hide: true },
                                        { field: 'nome_lista', width: 150, hide: true },
                                        { field: 'tipo_attivita', width: 150, headerName: "Attività" },
                                        { field: 'referente', width: 150, headerName: "Referente" },
                                        { field: 'consulente_interno', width: 150, hide: true },
                                        { field: 'fornitore_luce', width: 150, headerName: "Fornitore luce" },
                                        { field: 'fornitore_gas', width: 150, headerName: "Fornitore gas" },
                                        { field: 'fornitore_telefonia', width: 150, hide: true },
                                        { field: 'ultimo_contatto', width: 150, hide: true },
                                        { field: 'note_operatrice', width: 150, hide: true },
                                        { field: 'esito_chiamata', width: 150, hide: true },
                                        { field: 'esito_appuntamento', width: 150, hide: true },
                                        { field: 'assegnato_da', width: 150, hide: true },
                                        { field: 'altra_via', width: 150, hide: true },
                                        { field: 'città_spedizione', width: 150, hide: true },
                                        { field: 'provincia_spedizione', width: 150, hide: true },
                                        { field: 'cap_spedizione', width: 150, hide: true },
                                        { field: 'stato_spedizione', width: 150, hide: true },
                                        { field: 'nome_offerta_gas', width: 150, hide: true },
                                        { field: 'pdr', width: 150, hide: true },
                                        { field: 'matricola_contatore', width: 150, hide: true },
                                        { field: 'consumo_annuo', width: 150, hide: true },
                                        { field: 'attuale_fornitore', width: 150, hide: true },
                                        { field: 'indirizzo_di_fornitura', width: 150, hide: true },
                                        { field: 'numero', width: 150, hide: true },
                                        { field: 'città', width: 150, hide: true },
                                        { field: 'provincia', width: 150, hide: true },
                                        { field: 'cap', width: 150, hide: true },
                                        { field: 'nome_offerta_luce', width: 150, hide: true },
                                        { field: 'pod', width: 150, hide: true },
                                        { field: 'tensione_fornitura', width: 150, hide: true },
                                        { field: 'potenza_disponibile', width: 150, hide: true },
                                        { field: 'consumo_annuo_energia', width: 150, hide: true },
                                        { field: 'attuale_fornitore_energia', width: 150, hide: true },
                                        { field: 'indirizzo_di_fornitura_energia', width: 150, hide: true },
                                        { field: 'civico', width: 150, hide: true },
                                        { field: 'città_en', width: 150, hide: true },
                                        { field: 'provincia_en', width: 150, hide: true },
                                        { field: 'cap_en', width: 150, hide: true },
                                        { field: 'id_contratto', width: 150, hide: true },
                                        { field: 'codice_fiscale_firmatario', width: 150, hide: true },
                                        { field: 'modalità', width: 150, hide: true },
                                        { field: 'iban', width: 150, hide: true }
                                        ]}
                                        rows={
                                            this.state.contacts

                                        }
                                        components={{
                                            Toolbar: GridToolbar,
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="list-group">

                            {/* <TableOrder rows={this.state.contacts} setActiveContact={this.setActiveContact} /> */}
                        </div>

                        {/* <Button
                            className={`${classes.button} ${classes.removeAll}`}
                            size="small"
                            color="secondary"
                            variant="contained"
                            onClick={this.removeAllcontacts}
                        >
                            Remove All
          </Button> */}
                    </Grid>
                    <Grid item md={4}>
                        {currentContact ? (
                            <div className={classes.tutorial}>
                                <h4>Contatto</h4>
                                <div className={classes.detail}>
                                    <label>
                                        <strong>Nome:</strong>
                                    </label>{" "}
                                    {currentContact.nome}
                                </div>
                                <div className={classes.detail}>
                                    <label>
                                        <strong>Cognome:</strong>
                                    </label>{" "}
                                    {currentContact.cognome}
                                </div>
                                <div className={classes.detail}>
                                    <label>
                                        <strong>Codice fiscale:</strong>
                                    </label>{" "}
                                    {currentContact.codice_fiscale_firmatario}
                                </div>
                                <div className={classes.detail}>
                                    <label>
                                        <strong>Cellulare:</strong>
                                    </label>{" "}
                                    {currentContact.cellulare}
                                </div>

                                <Link
                                    to={"/contacts/" + currentContact.id}
                                    className={classes.edit}
                                >
                                    Modifica
                                </Link>
                            </div>
                        ) : (
                            <div>
                                <br />
                                <p className={classes.tutorial}>Seleziona un contatto</p>
                            </div>
                        )}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default withStyles(styles)(ContactComponent)